<script>
  import { onMount, createEventDispatcher } from "svelte";
  import { fade } from "svelte/transition";

  import MdChevronLeft from "svelte-icons/md/MdChevronLeft.svelte";
  import MdChevronRight from "svelte-icons/md/MdChevronRight.svelte";

  export let imagePath;
  export let alt;

  const dispatch = createEventDispatcher();
  let showImageNode;
  let expand = false;

  onMount(() => showImageNode.focus());

  const expandPreview = () => {
    if (!window.matchMedia("(min-width: 768px)").matches) return null;
    expand = !expand;
  };
</script>

<style lang="scss">div {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
}
div :global(svg) {
  cursor: pointer;
  z-index: 1;
  height: auto;
  color: #fff;
}
@media only screen and (max-width: 768px) {
  div {
    height: auto;
  }
}

@media only screen and (min-width: 768px) {
  .expand {
    position: fixed;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    min-width: 60rem;
    max-width: 134rem;
    width: 70%;
    height: 80%;
    max-height: 85rem;
    margin-top: 3.5rem;
  }
  .expand :global(svg) {
    display: none;
  }
  .expand img,
.expand video {
    width: 100%;
  }
}

img,
video {
  height: 100%;
  width: 70%;
  -o-object-fit: cover;
  object-fit: cover;
}

span {
  position: absolute;
  width: 99%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

@media only screen and (min-width: 768px) {
  .full-backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    cursor: pointer;
  }
}
/*# sourceMappingURL=src/components/GridImagePreview.svelte.map */</style>

<div
  tabindex="0"
  transition:fade
  bind:this={showImageNode}
  on:blur
  class:expand>
  <MdChevronLeft on:click={() => dispatch('prev')} />
  {#if imagePath.endsWith('.mp4')}
    <video src={imagePath} autoplay muted loop on:click={expandPreview} />
  {:else}
    <img src={imagePath} {alt} on:click={expandPreview} />
  {/if}
  <MdChevronRight on:click={() => dispatch('next')} />
</div>
<span class:full-backdrop={expand} />
