<script>
  import GridImagePreview from "../components/GridImagePreview.svelte";
  // import { fly } from 'svelte/transition';

  export let images = [];
  export let currImageindex;

  let showImage = false;

  const handleOpenShowImage = index => {
    currImageindex = index;
    showImage = true;
  };

  const handleNext = () => {
    if (currImageindex === images.length - 1) return (currImageindex = 0);
    currImageindex++;
  };

  const handlePrev = () => {
    if (currImageindex === 0) return (currImageindex = images.length - 1);
    currImageindex--;
  };
</script>

<style lang="scss">article {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: auto;
  border-radius: 10px;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  position: relative;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
}

img,
video {
  width: 49%;
  height: 49.5%;
  -o-object-fit: cover;
  object-fit: cover;
  cursor: pointer;
  -webkit-animation: fadeIn 1s ease-out;
  animation: fadeIn 1s ease-out;
}
img:nth-child(1),
video:nth-child(1) {
  border-top-left-radius: 10px;
}
img:nth-child(2),
video:nth-child(2) {
  border-top-right-radius: 10px;
}
img:nth-child(3),
video:nth-child(3) {
  border-bottom-left-radius: 10px;
}
img:nth-child(4),
video:nth-child(4) {
  border-bottom-right-radius: 10px;
}
img:not(:nth-child(2)), img:not(:last-child),
video:not(:nth-child(2)),
video:not(:last-child) {
  margin-bottom: 0.3rem;
  margin-right: 0.3rem;
}
@media only screen and (max-width: 325px) {
  img,
video {
    width: 100%;
    height: auto;
  }
}
/*# sourceMappingURL=src/components/GridImage.svelte.map */</style>

<article>
  {#each images as { text, image, alt }, i}
    {#if image.endsWith('.mp4')}
      <video
        src={image}
        autoplay
        muted
        loop
        on:click={() => handleOpenShowImage(i)} />
    {:else}
      <img src={image} {alt} on:click={() => handleOpenShowImage(i)} />
    {/if}
  {/each}
  {#if showImage}
    <GridImagePreview
      imagePath={images[currImageindex].image}
      alt={images[currImageindex].alt}
      on:blur={() => (showImage = false)}
      on:next={handleNext}
      on:prev={handlePrev} />
  {/if}
</article>
