<script>
  import { onDestroy } from "svelte";
  import {location} from 'svelte-spa-router';

  import FaBars from "svelte-icons/fa/FaBars.svelte";
  import MdMenu from "svelte-icons/md/MdMenu.svelte";

  import Logo from "./Logo.svelte";
  import GraphicsLogo from "./GraphicsLogo.svelte";
  import ConsultLogo from "./ConsultLogo.svelte";
  import NavLinks from "./NavLinks.svelte";
  import SideBar from "./SideBar.svelte";

  export let floatNavBar = false;
  export let currColor = "blue";

  let showSideNav = false;
  let hash = "#home";
  let timeout;

  $: currLogoComponent = () => {
    if (currColor === "orange") {
      return GraphicsLogo;
    } else if (currColor === "green") {
      return ConsultLogo;
    } else {
      return Logo;
    }
  };

  document.addEventListener("scroll", handleWindowScroll);

  onDestroy(() => {
    console.log("scroll evt");
    document.removeEventListener("scroll", handleWindowScroll);
  });

  function handleWindowScroll(e) {
    window.scrollY > 150 ? (floatNavBar = true) : (floatNavBar = false);
    if($location === '/cursos'){
      hash = null;
      return;
    } 

    let calc =
      (window.scrollY * 100) /
      (document.scrollingElement.scrollHeight - window.innerHeight);

    if (calc >= 19 && calc < 65) {
      hash = "#service";
    } else if (calc >= 65 && calc < 98) {
      hash = "#contact";
    } else if (calc >= 96) {
      hash = "#about";
    } else {
      hash = "#home";
    }
  }

  const handleCloseSideBar = () => {
    timeout = setTimeout(() => {
      showSideNav = false;
      clearTimeout(timeout);
    }, 200);
  };
</script>

