  justify-content: space-between;
  width: 100%;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 1440px;
  height: 7rem;
}
nav :global(figure) {
  margin-right: auto;
}
@media only screen and (max-width: 1024px) {
  nav :global(ul) {
    display: none;
  }
}
nav span {
  width: 3rem;
  margin-right: 5rem;
  color: #fff;
  cursor: pointer;
  display: none;
}
@media only screen and (max-width: 1024px) {
  nav span {
